.resume {
  font-size: 20px;
  font-weight: 500;
  margin-bottom: 40px;
  text-align: center;
}

.resume-link {
  text-decoration: none;
  color: pink;
  transition: 0.2s;
}

.resume-link:hover {
  color: rgb(255, 134, 154);
}

.small-text {
  font-size: 70%;
  color: var(--secondary);
}